import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {AoComponent} from './ao/ao.component';
import {CsdComponent} from './csd/csd.component';
import {MadComponent} from './mad/mad.component';
import {DsComponent} from './ds/ds.component';

const routes: Routes = [
    {
      path: 'ao',
      component:AoComponent
    },
    {
      path:'csd',
      component: CsdComponent
    },
    {
      path:'mad',
      component: MadComponent
    },
    {
      path:'ds',
      component: DsComponent
    }
  ]
  @NgModule({
      imports: [RouterModule.forChild(routes)],
      exports: [RouterModule]
    })
    export class ApplicationChildRoutingModule {
    }
    